import {
  Component,
  OnInit,
  OnChanges,
  Input,
  Output,
  EventEmitter,
  ChangeDetectionStrategy,
  ViewEncapsulation
} from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';

@Component({
  selector: 'hn-search',
  exportAs: 'hnSearch',
  preserveWhitespaces: false,
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  templateUrl: './hn-search.component.html'
})
export class HnSearchComponent implements OnInit {
  @Input() formList: any = [];

  @Input() keyword = 'searchContent';

  @Input() placeholder = 'è¯·è¾å¥æ¥è¯¢æ¡ä»¶';

  @Input() render: any;

  @Output() search: EventEmitter<any> = new EventEmitter();

  modeVisible: any = false;

  form: any = {};

  resetForm: any = [];

  validateForm: FormGroup;

  constructor(private fb: FormBuilder) {}

  ngOnInit() {
    this.form[this.keyword] = null;
    this.resetForm = JSON.parse(JSON.stringify(this.formList));
    this.initForm();
  }

  /**
   * åå§åè¡¨å
   */
  initForm() {
    const form: any = this.setInitForm(this.formList);
    this.validateForm = this.fb.group({ ...form });
  }

  /**
   * è®¾ç½®åå§åè¡¨åæ°æ®
   * @param arr æ°æ®
   */
  setInitForm(arr: any) {
    let form: any = {};
    arr.forEach(item => {
      if (item.children) {
        form = { ...form, ...this.setInitForm(item.children) };
      } else {
        if (item.type === 'checkbox') {
          form[item.key] = [Object.assign([], item.options)];
        } else {
          form[item.key] = [null];
        }
      }
    });
    return form;
  }

  /**
   * è¿æ»¤æ°ç»
   * @param data å¼ï¼æ°ç»
   */
  filterArr(data: any[]) {
    return data
      .filter(item => {
        return item.checked;
      })
      .map(item => {
        return item.value;
      });
  }

  /**
   * åæ¢é«çº§æç´¢
   */
  handleToggle(): void {
    this.modeVisible = !this.modeVisible;
  }

  /**
   * åæ¶æä½
   */
  handleCancle(): void {
    this.modeVisible = false;
  }

  /**
   * éç½®è¡¨å
   */
  handleReset(): void {
    this.formList = JSON.parse(JSON.stringify([...this.resetForm]));
    this.initForm();
    this.search.emit({ ...this.form });
  }

  /**
   * ç¡®è®¤æä½
   */
  handleConfirm(): void {
    this.submitForm();
  }

  /**
   * é®çenteräºä»¶
   */
  handleKeyupEnter(): void {
    this.submitForm();
  }

  /**
   * æäº¤è¡¨å
   */
  submitForm(): void {
    const value = this.validateForm.value;
    const params: any = { ...this.form, ...this.setParams(this.formList, value) };
    this.search.emit(params);
  }

  /**
   * è®¾ç½®data
   * @param data æ°æ®
   */
  setParams(arr: any, data: any) {
    let params = {};
    arr.forEach(item => {
      if (item.children) {
        params = { ...params, ...this.setParams(item.children, data) };
      } else {
        if (item.type === 'checkbox') {
          const str = this.filterArr(data[item.key]).join(',') || null;
          params[item.key] = str;
        } else {
          params[item.key] = data[item.key];
        }
      }
    });
    return params;
  }
}
