import {
  Component,
  OnInit,
  Input,
  Output,
  EventEmitter,
  TemplateRef,
  ChangeDetectionStrategy,
  ViewEncapsulation
} from '@angular/core';
import {
  trigger,
  state,
  style,
  animate,
  transition
  // ...
} from '@angular/animations';

@Component({
  selector: 'hn-scroll-list',
  exportAs: 'hnScrollList',
  preserveWhitespaces: false,
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  templateUrl: './hn-scroll-list.component.html',
  animations: []
})
export class HnScrollListComponent implements OnInit {
  // æ»å¨æ°æ®
  @Input() data: any[] = [];
  // æ»å¨é¡¹æ°é
  @Input() count = 6;
  // ä¸æ¬¡æ»å¨ä¸ªæ°
  @Input() scrollCount = 1;
  // æ»å¨é¡¹é«åº¦
  @Input() itemHeight = 60;
  // æ»å¨å¨ç»éåº¦
  @Input() speed = 300;
  // æ»å¨å»¶è¿
  @Input() delay = 2000;

  @Input() renderItem: TemplateRef<void>;
  // æ»å¨é¡¹clickäºä»¶
  @Output() click: EventEmitter<any> = new EventEmitter();

  y = 0;
  curr = 0;
  list: any = [];
  nextList: any = [];
  totalList: any = [];
  inAnimation = false;
  inEnter = false;

  initContentStyle = {
    transition: '',
    marginTop: '0px'
  };
  contentStyle = {};

  contentHeight: any;
  scrollHeight: any;
  listStyle: any;
  itemStyle: any;

  timer: any = null;

  constructor() {}

  ngOnInit() {
    this.initData();
    this.reset();
  }

  initData() {
    this.contentHeight = this.itemHeight * this.count;
    this.scrollHeight = this.scrollCount * this.itemHeight;
    this.listStyle = {
      height: this.contentHeight + 'px'
    };
    this.itemStyle = {
      height: this.itemHeight + 'px'
    };
  }

  /**
   * éç½®æ»å¨
   */
  reset() {
    this.clearScroll();
    this.y = 0;
    this.curr = 0;
    this.inAnimation = false;
    this.contentStyle = { ...this.initContentStyle };
    this.loop();
  }

  /**
   * é¼ æ overæä½
   */
  handleMouseover() {
    this.inEnter = true;
    this.clearScroll();
  }

  /**
   * é¼ æ ç§»åºæä½
   */
  handleMouseout() {
    this.inEnter = false;
    this.loop();
  }

  /**
   * å¨ç»ç»æ
   */
  handleEnd() {
    this.inAnimation = false;
    this.curr = 0;
    this.contentStyle = { ...this.initContentStyle };
    this.loop();
  }

  /**
   * æ»å¨å¨ç»
   */
  loop() {
    if (this.inEnter) {
      return;
    }
    this.getList();
    this.clearScroll();
    if (this.data.length >= this.count) {
      this.getNextList();
      this.totalList = [...this.list, ...this.nextList];

      this.timer = setTimeout(() => {
        this.inAnimation = true;
        this.y += this.scrollCount;
        this.curr += this.scrollCount;
        this.contentStyle = {
          transition: this.inAnimation ? this.speed + 'ms' : '',
          marginTop: -(this.curr * this.itemHeight) + 'px'
        };
      }, this.delay);
    }
  }

  /**
   * æ¸é¤æ»å¨
   */
  clearScroll() {
    if (this.timer) {
      clearTimeout(this.timer);
    }
  }

  /**
   * è·ååè¡¨
   */
  getList() {
    const { y, count, data } = this;
    const curr = y % data.length;
    let list = data.slice(curr, curr + count);
    // è¡¥ä¸è¶³
    if (list.length < count && this.data.length >= count) {
      list = [...list, ...data.slice(0, count - list.length)];
    }
    this.list = list;
  }

  /**
   * è·åä¸é¨ååè¡¨
   */
  getNextList() {
    const { y, count, scrollCount, data } = this;
    const curr = (y + count) % data.length;
    let nextList = data.slice(curr, curr + scrollCount);
    // è¡¥ä¸è¶³
    if (nextList.length < scrollCount) {
      nextList = [...nextList, ...data.slice(0, scrollCount - nextList.length)];
    }
    this.nextList = nextList;
  }

  /**
   * ç¹å»æ»å¨é¡¹
   * @param row æ»å¨é¡¹æ°æ®
   */
  handleClick(row: any) {
    this.click.emit(row);
  }
}
